import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: () => import("../views/Home.vue")
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("../views/Login")
  },
  {
    path: "/dashboard",
    name: "Dashboard",
    redirect: { name: "Dashboard.summary" },
    component: () => import("../views/Dashboard"),
    children: [
      {
        path: "summary",
        name: "Dashboard.summary",
        component: () => import("../views/Dashboard/Summary")
      },
      {
        path: "group",
        name: "Dashboard.group",
        component: () => import("../views/Dashboard/Group")
      },
      {
        path: "company",
        name: "Dashboard.company",
        component: () => import("../views/Dashboard/Company")
      },
      {
        path: "location",
        name: "Dashboard.location",
        component: () => import("../views/Dashboard/Location")
      },
      {
        path: "medicine",
        name: "Dashboard.medicine",
        component: () => import("../views/Dashboard/Medicine")
      }
    ]
  }
];

const router = new VueRouter({
  //mode: "history",
  base: process.env.BASE_URL,
  routes,
  linkActiveClass: "active"
});

export default router;
